  chunkArray(arr: any[], chunkSize: number): any {
    const chunks: any[] = [];
    for (let i = 0; i < arr.length; i += chunkSize) {
      chunks.push(arr.slice(i, i + chunkSize));
    }
    return chunks;
  }

  createObjectWithChunks(
    arr: any[],
    chunkSize: number,
  ): { [key: number]: any[] } {
    const chunks = this.chunkArray(arr, chunkSize);
    const obj: { [key: number]: any[] } = {};
    // for (let i = 0; i < chunks.length; i++) {
    //   obj[i] = chunks[i];
    // }
    let position = 1; // Initialize position counter
    for (let i = 0; i < chunks.length; i++) {
      obj[i] = chunks[i].map((item: any) => ({
        position: position++,
        value: item,
      }));
    }
    return obj;
  }

