import { Component, OnInit, Output, EventEmitter, inject } from '@angular/core';
import { Location } from '@angular/common';
import {
  ProgramDataService,
  SelectedExercisesService,
  ShareClientsDataService,
} from '@slowautomobile/exercises';
import {
  ControlContainer,
  FormGroup,
  FormsModule,
  ReactiveFormsModule,
} from '@angular/forms';
import { Subscription } from 'rxjs';
import { MatSlideToggle } from '@angular/material/slide-toggle';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { MatInput } from '@angular/material/input';
import { MatIcon } from '@angular/material/icon';
import {
  MatFormField,
  MatLabel,
  MatPrefix,
  MatHint,
} from '@angular/material/form-field';
import { SelectedExpansionPanelComponent } from '@slowautomobile/exercises';

@Component({
    selector: 'sanareapp-edit-program',
    templateUrl: './edit-program.component.html',
    styleUrl: './edit-program.component.scss',
    imports: [
        FormsModule,
        ReactiveFormsModule,
        MatFormField,
        MatLabel,
        MatIcon,
        MatPrefix,
        MatInput,
        MatHint,
        CdkTextareaAutosize,
        MatSlideToggle,
        SelectedExpansionPanelComponent,
    ]
})
export class EditProgramComponent implements OnInit {
  private location = inject(Location);
  private selectedExercisesService = inject(SelectedExercisesService);
  private programDataService = inject(ProgramDataService);
  private controlContainer = inject(ControlContainer);
  private shareClientsDataService = inject(ShareClientsDataService);

  selectedExercises = this.selectedExercisesService.selectedExercises;
  form!: FormGroup;
  panelForms: FormGroup[] = [];
  subscriptions = new Subscription();

  programDataForm!: FormGroup;
  programData = this.programDataService.programData;
  @Output() isNameValid = new EventEmitter<boolean>();
  clientInfo = this.shareClientsDataService.clientInfo;
  reuseProgram = this.shareClientsDataService.reuseProgram;
  isBrandNewClientsProgram =
    this.shareClientsDataService.isBrandNewClientsProgram;

  // temporary !!!
  ngOnInit(): void {
    this.form = this.controlContainer.control?.get('programInfo') as FormGroup;
  }

  get programForm() {
    return this.form.controls;
  }

  onBack() {
    this.location.back();
  }

  pdfLayouts = [
    { value: 'option-0', viewValue: '3 columns / 2 rows (default)' },
    { value: 'option-1', viewValue: '1 col / 6 rows' },
  ];

  selected = 'option-0';
}
