import { Component, inject, Input, OnDestroy, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import {
  Exercise,
  ExercisesService,
  SelectedExercisesService,
  ShareDataService,
  ShareStateService,
} from '@slowautomobile/exercises';
import { CdkDropListGroup } from '@angular/cdk/drag-drop';
import { Subscription, concatMap, of, skip } from 'rxjs';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { UsersService } from '@slowautomobile/users';

import {
  ExerciseSearchBtnsWrapperComponent,
  ThemeManagerService,
} from '@slowautomobile/ui';
import { ExercisesGridComponent } from '../exercises-grid/exercises-grid.component';
import { SelectedExercisesListComponent } from '../selected-exercises-list/selected-exercises-list.component';
import { OverlayscrollbarsModule } from 'overlayscrollbars-ngx';

type ListOption = 'all' | 'selected' | 'favorite';

@Component({
  selector: 'sanareapp-exercises-list',
  templateUrl: './exercises-list.component.html',
  imports: [
    CdkDropListGroup,
    ExerciseSearchBtnsWrapperComponent,
    ExercisesGridComponent,
    SelectedExercisesListComponent,
    OverlayscrollbarsModule,
  ],
})
export class ExercisesListComponent implements OnInit, OnDestroy {
  private exercisesService = inject(ExercisesService);
  private shareDataService = inject(ShareDataService);
  private selectedExercisesService = inject(SelectedExercisesService);
  private location = inject(Location);
  private usersService = inject(UsersService);
  themeManager = inject(ThemeManagerService);
  private shareStateService = inject(ShareStateService);

  listOptionSignal = this.shareStateService.listOption;
  isFetchingData = this.shareStateService.isFetchingData;
  // listOption: ListOption = 'all';

  exercises!: Exercise[];
  subscriptions = new Subscription();
  themeSignal = this.themeManager.themeSignal;
  selectedExercises = this.selectedExercisesService.selectedExercises;
  @Input() editMode!: boolean;
  userFavorites = this.usersService.userFavorites;
  // userId = this.usersService.userId;

  ngOnInit(): void {
    // this._getExercises();
    // This can be part of async solution, but it doesnt solve double binded name editing!!!
    // this.exercises$ = this.shareDataService.searchedData$.pipe(map(searchResults => {
    //   const exercises = [];

    //   for (const exercise of searchResults) {
    //     if (
    //       this.selectedExercises().some(
    //         selectedExercises => selectedExercises.id === exercise.id,
    //       )
    //     ) {
    //       exercise.isSelected = true;
    //       exercises.push(exercise);
    //     } else {
    //       exercise.isSelected = false;
    //       exercises.push(exercise);
    //     }
    //   }

    //   return exercises;
    // }));
    console.log(this.editMode);
    if (this.editMode) {
      this.listOptionSignal.set('selected');
    }

    // Count subscriptions index so that third one is not calling this._getSelectedExercises(), but refreshes exercises to new search, even though it is in edit mode
    let countSubsIndex = 0;

    // Skip first emit in ngOnInit in exercise-list component, catch the second emit coming from search component. If editMode===true, initial listOption will be 'selected', and if searchData$ has changed, listOption is set to 'all'
    const subscription = this.shareDataService.searchedData$
      .pipe(
        skip(1),
        concatMap((value, index) => {
          if (index === 0) {
            countSubsIndex++;
            console.log('second emit');
          } else if (index >= 1) {
            countSubsIndex++;
            console.log('third and on emits');
            this.listOptionSignal.set('all');
            // this.changeListedItems();
          }
          console.log(this.exercises);
          return of(value);
        }),
      )
      .subscribe(searchResults => {
        console.log('subscriptio two the same');

        const start = performance.now();
        // if (!searchResults || searchResults.length === 0) {
        //   console.log('!searchResults || searchResults.length === 0');
        //   return;
        // }
        console.log('step1');
        this.exercises = [];
        for (const exercise of searchResults) {
          // Check if exercise is favorite for user
          if (
            this.userFavorites().favExercises.some(
              favExercise => favExercise === exercise.id,
            )
          ) {
            exercise.isFavorite = true;
          } else {
            exercise.isFavorite = false;
          }

          // Check if exercise is selected/part of existing program and add to exercises array
          if (
            this.selectedExercises().some(
              selectedExercises => selectedExercises.id === exercise.id,
            )
          ) {
            exercise.isSelected = true;

            this.exercises.push(exercise);
          } else {
            exercise.isSelected = false;
            this.exercises.push(exercise);
          }
        }

        this.isFetchingData.set(false);
        console.log('step2');

        if (this.editMode && countSubsIndex <= 1) {
          // this.listOption = 'selected';
          this._getSelectedExercises();
        }
        console.log(this.exercises);
        console.log(performance.now() - start);
      });
    this.subscriptions.add(subscription);
    console.log('step4');

    // this.windowSize$ = fromEvent(window, 'resize').pipe(
    //   startWith(null), // Emit an initial value to trigger the styles on component initialization
    //   debounceTime(200),
    //   map(() => window.innerWidth),
    // );
  }

  changeListedItems() {
    if (this.listOptionSignal() === 'all') {
      console.log('subscriptio one');
      const subscription = this.shareDataService.searchedData$.subscribe(
        searchResults => {
          // this.listOption = 'all';
          this.exercises = [];
          for (const exercise of searchResults) {
            // Check if exercise is favorite for user
            if (
              this.userFavorites().favExercises.some(
                favExercise => favExercise === exercise.id,
              )
            ) {
              exercise.isFavorite = true;
            } else {
              exercise.isFavorite = false;
            }

            if (
              this.selectedExercises().some(
                selectedExercises => selectedExercises.id === exercise.id,
              )
            ) {
              exercise.isSelected = true;
              this.exercises.push(exercise);
            } else {
              exercise.isSelected = false;
              this.exercises.push(exercise);
            }
          }
        },
      );
      this.subscriptions.add(subscription);
    } else if (this.listOptionSignal() === 'selected') {
      this._getSelectedExercises();
    } else if (this.listOptionSignal() === 'favorite') {
      this._getFavoriteExercises();
    }
  }

  _getSelectedExercises() {
    this.isFetchingData.set(true);

    this.exercises = [];
    this.selectedExercises().forEach(selectedExercise => {
      if (
        this.userFavorites().favExercises.some(
          favExercise => favExercise === selectedExercise.id,
        )
      ) {
        selectedExercise.isFavorite = true;
      } else {
        selectedExercise.isFavorite = false;
      }

      this.exercises.push(selectedExercise);
    });
    this.isFetchingData.set(false);
  }

  _getFavoriteExercises() {
    this.isFetchingData.set(true);

    const subscription = this.exercisesService
      .getExercises()
      .subscribe(exercises => {
        this.exercises = [];
        for (const exercise of exercises) {
          // Check if exercise is selected/part of existing program and add to exercises array
          if (
            this.selectedExercises().some(
              selectedExercises => selectedExercises.id === exercise.id,
            )
          ) {
            exercise.isSelected = true;
          } else {
            exercise.isSelected = false;
          }

          // Check if exercise is favorite for user
          if (
            this.userFavorites().favExercises.some(
              favExercise => favExercise === exercise.id,
            )
          ) {
            exercise.isFavorite = true;
            this.exercises.push(exercise);
          } else {
            exercise.isFavorite = false;
          }
        }
        this.isFetchingData.set(false);
      });
    this.subscriptions.add(subscription);
  }

  onBack() {
    // console.log(event)
    this.location.back();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();

    // Reset selectedExercises state to empty array
    // this.selectedExercises.set([]);
  }
}
